/* eslint-disable @typescript-eslint/strict-boolean-expressions */
/* eslint-disable @typescript-eslint/restrict-template-expressions */
import * as fs from 'fs'
import * as path from 'path'
import {
  createConnection,
  ProposedFeatures,
  TextDocuments,
  _Connection,
  TextDocumentChangeEvent,
  Diagnostic,
  DiagnosticSeverity
} from 'vscode-languageserver/node'
import * as FPCC from 'node-float-pigment-css'
import { URI } from 'vscode-uri'

import {
  TextDocument
} from 'vscode-languageserver-textdocument'
import debounce = require('licia/debounce')
import { SEND_TO_SKYLINE_LSP_CLIEND_ID } from '../lib/constants'

// do not bundle some require by webpack
// @ts-expect-error
const requireFunc = typeof __webpack_require__ === 'function' ? __non_webpack_require__ : require

class SkylineStyleServer {
  private readonly connection: _Connection
  private readonly documents: TextDocuments<TextDocument>
  private readonly requestResolveMap: Map<string, (value: boolean | PromiseLike<boolean>) => void> = new Map()
  private readonly cacheMap: Map<string, number> = new Map()
  private fpccPath?: string
  private fpcc: typeof FPCC | null = null
  constructor () {
    this.connection = createConnection(ProposedFeatures.all)

    this.documents = new TextDocuments(TextDocument)
    this.init()
  }

  sendMsgToClient (data: ILSPNotificationData): void {
    this.connection.sendNotification(SEND_TO_SKYLINE_LSP_CLIEND_ID, data)
  }

  // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
  get logger () {
    return this.connection.console
  }

  init (): void {
    this.onMessage()
    this.connection.onInitialize((params) => {
      this.fpccPath = process.env.FPCC_PATH as string
      if (fs.existsSync(this.fpccPath ?? '')) {
        this.fpcc = requireFunc(this.fpccPath)
      }
      return {
        capabilities: {}
      }
    })
    this.documents.onDidChangeContent(change => {
      this.validateTextDocument(change)
    })
    this.documents.onDidOpen(change => {
      this.validateTextDocument(change)
    })
  }

  onMessage (): void {
    this.connection.onNotification(SEND_TO_SKYLINE_LSP_CLIEND_ID, (payload: ILSPNotificationData) => {
      switch (payload.command) {
        case 'checkIsSkylineFile': {
          if (this.requestResolveMap.has(payload?.data?.path)) {
            if (payload.data.result) {
              this.cacheMap.set(payload.data.path, Date.now())
            }
            const res = this.requestResolveMap.get(payload.data.path)
            res?.(payload.data.result)
            this.logger.log(`res, ${payload.data.path}, ${payload.data.result}`)
            this.requestResolveMap.delete(payload?.data?.path)
          }
        }
      }
    })
  }

  /**
   * for one path, there will be a 5s cache
   *
   * if expired, we will send message to wechatdevtools for checking
   *
   * if not, update cache time
   * */
  checkIfCacheIsExpired (filePath: string): boolean {
    if (this.cacheMap.has(filePath)) {
      const lastExpiredTime = this.cacheMap.get(filePath) ?? 0
      const now = Date.now()
      if (now - lastExpiredTime > 5000) { // 10s cache
        return true
      } else {
        this.cacheMap.set(filePath, now)
        return false
      }
    }
    return true
  }

  checkIfIsSkylineStyleFile = async (fileURI: URI): Promise<boolean> => {
    return await new Promise((resolve) => {
      try {
        const extName = path.extname(fileURI.path)
        const filePath = fileURI.path
        // if expired, ask devtools for result
        if (this.checkIfCacheIsExpired(filePath)) {
          const jsonPath = filePath.split(extName).join('.json')
          if (fs.existsSync(jsonPath)) {
            this.requestResolveMap.set(filePath, resolve)
            this.sendMsgToClient({
              command: 'checkIsSkylineFile',
              data: {
                path: fileURI.path
              }
            })
          } else {
            return resolve(false)
          }
        } else {
          // if we get cache, return true
          return resolve(true)
        }
      } catch (e) {
        this.logger.log(e as string)
        return resolve(false)
      }
    })
  }

  validateTextDocument = debounce(async (change: TextDocumentChangeEvent<TextDocument>) => {
    if (!this.fpcc) {
      return
    }
    const textDocument = change.document
    const text = textDocument.getText()
    const uri = textDocument.uri
    const fileURI = URI.parse(uri)
    if (!await this.checkIfIsSkylineStyleFile(fileURI)) {
      this.logger.log(`fsPath ${fileURI.fsPath} check failed`)
      this.connection.sendDiagnostics({
        uri,
        diagnostics: []
      })
      return
    }
    const res = await this.fpcc?.compileSingle({
      fileContent: Buffer.from(text),
      fileName: uri,
      outputType: 'none'
    })
    const diagnostics: Diagnostic[] = []
    if (res?.warnings && res.warnings.length > 0) {
      for (const warn of res.warnings) {
        const diagnostic: Diagnostic = {
          severity: DiagnosticSeverity.Warning,
          range: {
            start: {
              line: warn.start.line,
              character: warn.start.column
            },
            end: {
              line: warn.end.line,
              character: warn.end.column
            }
          },
          message: `[WXSS Skyline Warning] ${warn.message}`
        }
        diagnostics.push(diagnostic)
      }
    }
    this.logger.log(`res, ${JSON.stringify(res?.warnings ?? null, null, 2)}`)
    this.connection.sendDiagnostics({
      uri,
      diagnostics
    })
  }, 200)

  start (): void {
    this.documents.listen(this.connection)
    this.connection.listen()
  }
}

const skylineStyleServer = new SkylineStyleServer()

skylineStyleServer.start()
